#game {
  background-color: #fbe461;
  overflow: hidden;
}

#gallery {
  display: flex;
  padding: 12pt;
  background-color: #ffffff;
}

.haiang_workplace {
  flex: 1;
  flex-grow: 0;
  flex-shrink: 1;
}

.frame {
  display: inline-block;
  border: 1pt solid black;
  padding: 3pt;
  margin: 3pt;
}

.haiang_work {
  position: relative;
  cursor: pointer;
}

div,
img {
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
}

.gallery_button {
  flex: 1;
  flex-grow: 1;
  flex-shrink: 1;
  padding: 3pt;
}

#chat_box {
  background-color: rgb(161, 127, 255);
}

.grid_unit {
  width: 3vw;
  height: 3vw;
}

.sb-soundplayer-play-btn {
  width: 2.5em;
}

#walking_cats_mail,
#walking_cats_mail_unopen {
  width: 250px;
}

#bears_photos {
  width: 450px;
}

li {
  display: inline;
}

mark {
  background-color: #ffffff00;
  color: #009688;
}
